@use "../global/variables.scss" as *;

.site-navigation {
  margin: 0;
  padding: 0;

  list-style-type: none;
  outline: 2px solid #dda0dd;
  outline-offset: -2px;
}

.site-navigation .navigation__link {
  min-height: 79px;
  min-width: 160px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  color: $color-black;
  text-decoration: none;
  outline: 2px solid #630363;
  // outline-offset: -4px;
}

// от 768
@media (min-width: $tablet-width) {
  .site-navigation {
    display: flex;
  }

  .site-navigation .navigation__link {
    // display: flex;
    min-width: 160px;
    min-height: 88px;
  }
}

// от 1150
@media (min-width: $desktop-width) {
  .site-navigation {
    // display: flex;
    width: 440px;
  }

  .site-navigation .navigation__link {
    // display: flex;
    width: 220px;
    // min-height: 88px;
  }
}
