@use "../global/variables.scss" as *;

.user-navigation {
  margin: 0;
  padding: 0;


  list-style-type: none;
  outline: 2px solid #dda0dd;
  outline-offset: -2px;
}

.user-navigation .navigation__link {
  min-height: 79px;
  min-width: 174px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  color: $color-black;
  text-decoration: none;
  outline: 2px solid #630363;
  // outline-offset: -4px;
}

@media (min-width: $tablet-width) {
  .user-navigation {
    // margin-bottom: 20px;
    // display: flex;
    display: grid;
    // justify-content: center;
    grid-template-columns: 82px 265px;
  }

  .user-navigation .navigation__link {
    justify-content: center;
    min-width: 82px;
    min-height: 88px;
  }
}
